<template>
    <!--<div @mouseover="play" @mouseleave="stop">-->
    <div>
        <video ref="videoPlayer" class="video-js vjs-theme-sea" 
        >
            
             <track kind="captions" :src="`${publicPath}text2.vtt`" srclang="en" label="English" default>

        </video>
    </div>
</template>

<script>
import videojs from 'video.js';
import 'videojs-youtube/dist/Youtube.min.js';
import 'video.js/dist/video-js.min.css';
import 'video.js/dist/video.min.js';
import '@videojs/themes/dist/sea/index.css';
import 'videojs-landscape-fullscreen/dist/videojs-landscape-fullscreen.min.js';

export default {
    name: "VideoPlayer",
    props: {
        options: {
            type: Object,
            default() {
                return {};
            }
        }
    },
    data() {
        return {
            publicPath: process.env.BASE_URL,
            player: null
        }
    },
    mounted() {
        this.player = videojs(this.$refs.videoPlayer, this.options, function onPlayerReady() {
            console.log('onPlayerReady', this);
        })

        this.player.landscapeFullscreen({
          fullscreen: {
            enterOnRotate: true,
            alwaysInLandscapeMode: true,
            iOS: true
          }
        })
        
    },
    beforeDestroy() {
        if (this.player) {
            this.player.dispose()
        }
    },
    methods: {
        play() {
         //if (this.player.paused())
           this.player.play(); 
         //else 
          // this.player.pause();
        },

        stop() {
           this.player.pause(); 
        }        
    }    
}
</script>

